import React, {useState} from 'react';
import Axios from 'axios';
import {v4 as uuidv4} from 'uuid';
import "./App.css";
import Recipe from './components/Recipe';
import Alert from './components/Alert'
import Login from './components/Login'

const App = () => {
    const[query, setQuery] = useState("");
    const[recipes, setRecipes] = useState([]);
    const[alert, setAlert] = useState("");

   // var recipeSearchValue = 'pies'; //$('#recipeSearchText').val();
    const appId = "59d201f4";
    const appKey = "a43629aaaebb6aef4e036fee8960388b";
    const url = `https://api.edamam.com/search?q=${query}&app_id=${appId}&app_key=${appKey}&from=0&to=50&calories=591-722&health=alcohol-free`;

    const getData = async () => {
        if(query !== "" || query.length === 0){
            const result = await Axios.get(url);
            if(!result.data.more){
                return setAlert("No Food with such name");
            }
            setRecipes(result.data.hits)
            console.log(result);
            setAlert("");
            setQuery(""); // clears the Search TextBox value
        } else {
            setAlert("Please fill the form");
        }
    }

    const onChange = (e) => {
        setQuery(e.target.value);
    }

    const onSubmit = (e) => {
        e.preventDefault();
        getData();
    }

    return (
        <div>
        <span className="page-title">
        <h1 style={{marginLeft: '1em'}} className="" onClick={getData}>Meal Planner</h1>
        <h4 style={{marginLeft: '5em', textTransform: 'none'}}>(Assistant)</h4>
        
        
        <form className="search-form"  onSubmit={onSubmit}>
        {alert !=="" && <Alert alert={alert}/>} 
                <input type="text" placeholder="Search Food" 
                                autoComplete="off" onChange={onChange}
                                value={query} />
                 <input type="submit" value="search" />     
                 
                 {alert !=="" && <Alert alert={alert}/>} 
            </form>
   
            <Login/>
            </span>
        <div className="App">
      
            
            <div className="recipes">
                {recipes !== [] && recipes.map(recipe => 
                    <Recipe key={uuidv4()} recipe={recipe} />)}
            </div>

        </div>
    </div>
    );
};

export default App;
